@media screen and (max-width: 1000px) {
  .logo {
    background-image: linear-gradient(90deg, rgb(104, 224, 98), rgb(48, 164, 50)) !important;
    background-clip: text;
    color: transparent;
    margin: 10px;
  }

  #overlay {
    display: none;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }
  
  body {
    height: 100vh;
    background-color: rgb(21, 21, 21);
    font-family: 'Montserrat', sans-serif;
    color: white;
  }
  
  .header--main {
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: space-between;
    background-color: rgb(34, 32, 32);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 90px;
  
  }

  
  .header--main ul {
    color: white;
  }
  
  .header--main p {
    font-size: 1.8rem;
    margin-left: 30px;
    cursor: pointer;
    color: white;
    background-image: linear-gradient(90deg, rgb(141, 8, 243), rgb(5, 222, 255));
    background-clip: text;
    color: transparent;
    font-weight: 700;
  }
  
  .header--main > nav {
    padding: 30px;
    display: flex;
    align-items: center;
    text-align: center;
    list-style: none;
    font-size: 1.5rem;
    color: white;
  }
  
  .header--main > nav > a {
    margin : 20px;
    text-decoration: none;
    color: white;
  }
  
  main {
    margin-top: 50px;
    width: 100vh;
    overflow: hidden;
  }

    
  main h2 {
    text-align: center;
    font-size: 3rem;
  }
  
  main h3 {
    text-align: center;
  }
  
  main .greeting {
    background-image: url('../images/matrix-g7eee74277_1920.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
  }
  
  main .greeting--image {
    object-fit: contain;
    object-position: right;
    width: 50%;
  }
  
  main .greeting--text {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 10px 30px;
    background-color: rgba(0, 0, 0, 0.8);
    font-size: 3.5rem;
  }
  
  .about-me {
    text-align: center;
  }


  .about-me p {
    text-align: left;
  }
  
  .about-section {
    margin-top: 100vh;
    font-size: 1.6rem;
    text-align: left;
  }
  
  .grid-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .grid-item {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .project {
    display: flex;
    align-items: center;
    margin: auto;
    margin: 50px;
    flex: 1;
  }

  .project img {
    width: 50%;
  }

  .project-description {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex: 1;
  }

  .project-description p { 
    width: 60%;
    font-size: 20px;
  }

  .project-description h2 {
    margin-top: 0px;
    font-size: 33px;
  }

  .project-button {
    width: 70%;
    display: flex;
    justify-content: space-evenly;
    gap: 10px;
  }

  .project-button button {
    display: flex;
    align-items: center;
    font-size: 17px;
    padding: 5px 20px;
    font-weight: 700;
    border: none;
    background-color: green;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
  }

  .project-button button:hover {
    opacity: 0.7;
  }

  .project-button img {
    margin: 0 10px;
    width: 30px;
    height: 30px;
  }
  
  .middle {
    transition: .0.7s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    width: 300px;
  }

  .middle button {
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    border-radius: 5px;
    padding: 5px 20px;
    font-size: 15px;
    font-weight: 700;
    border: none;
    transition: transform 0.15s;
    white-space: nowrap;
    background-color: black;
    color: white;
  }
  
  .grid-image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
  }
  
  .name-of-project {
    margin-bottom: 50px;
    font-size: 20px;
  }
  
  .text {
    margin: 0px;
    padding: 10px 22px;
    background-color: #c47713;
    color: rgb(255, 255, 255);
  }
  
  .url-to-my-work {
    text-decoration: none;
    background-image: linear-gradient(50deg, rgb(230, 0, 255), rgb(0, 213, 255));
    background-clip: text;
    color: transparent;
    font-weight: 700;
  }

  .project-name-mobile {
    display: none;
  }

  .button-mobile {
    display: none;
  }
}